import React from 'react';
import { TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>

                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="black"
                    barTintColor="white"
                    
                >
                    <TabBar.Item                       
                        icon={
                            <i className='iconfont icon-home1'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-home2'></i>                           
                        }
                        title="首页"
                        key="home"
                        selected={ pathname === '/Home' }
                        onPress={ () => {
                            this.props.history.push('/Home');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-gouwuchekong'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-gouwuche'></i>                           
                        }
                        title="购物车"
                        key="cart"
                        selected={ pathname === '/Car' }
                        onPress={ () => {
                            this.props.history.push('/Car')
                        } }
                        data-seed="logId1"
                    >

                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-dingdan2'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-dingdan1'></i>
                        }
                        title="订单列表"
                        key="Friend"
                        selected={ pathname === '/List' }
                        onPress={ () => {
                            this.props.history.push('/List')
                        } }
                    >                       
			        </TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont icon-wode'></i> }
                        selectedIcon={<i className='iconfont icon-wode1'></i> }
                        title="我的淘宝"
                        key="my"
                        selected={ pathname === '/My' }
                        onPress={ () => {
                            this.props.history.push('/My')                          
                        } }
                    >                       
			        </TabBar.Item>
                   
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);