import React from 'react'
import { withRouter } from 'react-router-dom'
import TableBar from './TableBar'

const Xq = (props) => {
    return (
        <div>
            
            <img
                src={`https://gw.alicdn.com/bao/uploaded/i3/206304802/O1CN01pLF2jp1lLMXQzmRT0_!!206304802.jpg_500x500q90.jpg`}
                alt=""
                style={{width: '100%', 
                        position:'absolute',
                        top:0,                 
                        left: 0,
                }}
            />
            <p 
                style={{
                    zIndex:'98',
                    top:'120px',                 
                    marginLeft: '20px',
                    backgroundColor:'black',
                    opacity:0.3, 
                    color:'white',
                    height:'30px',
                    width:'30px',
                    borderRadius:'30px',
                    fontSize:'25px',
                    textAlign:'center',
                }}
                onClick={ () => {
                    props.history.push('/Home');
                } }
            > &lt; </p>
            <TableBar/>
        </div>
    )
}

export default withRouter(Xq)
