import React from 'react';
import {BrowserRouter as Router,Redirect,Route,Switch} from 'react-router-dom';
import {Button,TabBar} from 'antd-mobile';
import Mytab from './taobao/Mytab';
import Home from './taobao/Home';
import Car from './taobao/Car';
import List from './taobao/List';
import My from './taobao/My';
import Search from './taobao/Search';
import Xq from './taobao/Xq';


const App=()=>{
	return <Router>
		<Switch>
			<Route exact path='/home' component={Home} />
			<Route path='/car' component={Car} />
			<Route path='/list' component={List}/>
			<Route path='/my' component={My}/>
			<Route path='/search' component={Search}/>
			<Route path='/xq' component={Xq}/>
		</Switch>
		<Redirect from='/' to='/Home'/>
	</Router>
}




export default App