import { TabBar } from 'antd-mobile'
import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'

class TableBar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0,
            } }>

                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="black"
                    barTintColor="white"
                    
                >
                    <TabBar.Item                       
                        icon={
                            <i className='iconfont icon-shop'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shop'></i>                           
                        }
                        title="店铺"
                        key="shop"
                        
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-wang'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-wang'></i>                           
                        }
                        title="客服"
                        key="wang"
                        
                    >

                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-favor'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-favor'></i>
                        }
                        title="收藏"
                        key="favor"
                        
                    >                       
			        </TabBar.Item>
                    <TabBar.Item 
                        icon={
                            <div>
                                <p style={{
                                    backgroundColor:'rgb(255,125,42)',
                                    height:'40px',
                                    width:'100px',
                                    color:'white',
                                    paddingRight:0,
                                    borderTopLeftRadius:'50%',
                                    borderBottomLeftRadius:'50%',
                                    textAlign:'center',
                                    lineHeight:'35px',
                                }}>
                                    加入购物车
                                </p> 
                            </div>
                        }                       
                    >                                          
                    </TabBar.Item>
                    
                    <TabBar.Item
                        icon={<div>
                            <p style={{
                                backgroundColor:'rgb(255,78,34)',
                                paddingLeft:'0',
                                height:'40px',
                                width:'100px',
                                color:'white',
                                borderTopRightRadius:'50%',
                                borderBottomRightRadius:'50%',
                                textAlign:'center',
                                lineHeight:'35px',
                            }}>
                                立即购买
                            </p>
                        </div>}
                    >
                        
                    </TabBar.Item>
                    
                </TabBar>
                
               
            </div>
        );
    }
}

export default withRouter(TableBar);
